@import "https://fonts.googleapis.com/css2?family=Cairo:wght@300;700&family=Oswald:wght@300;700&display=swap";
:root {
  --background-dark: #020304;
  --spaceblue: #011b45;
  --darkerblue: #02132e;
  --ligtherblue: #042252;
  --violet: #5e2f64;
  --lighterviolet: #9c6ba8;
  --accentpink: #ffa8e2;
  --silver: #d9d9d9;
  --moonglow: #f4f4f4;
  --green: #32cd32;
  --red: #ff4500;
}

body {
  background-color: var(--background-dark);
  color: var(--moonglow);
  margin: 0;
  padding: 0;
  font-family: Cairo, sans-serif;
  overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: var(--darkerblue);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--accentpink);
  border: 3px solid var(--darkerblue);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--lighterviolet);
}

#loginPopup {
  z-index: 10;
  background: #000000d9;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#loginForm {
  background: var(--spaceblue);
  text-align: center;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 8px 20px #000000b3;
}

#loginForm h2 {
  color: var(--accentpink);
  margin-bottom: 20px;
  font-family: Cairo, sans-serif;
  font-size: 1.8em;
}

#loginForm input {
  background: var(--ligtherblue);
  width: 80%;
  color: var(--silver);
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 10px;
  font-family: Cairo, sans-serif;
  font-size: 1em;
}

.region {
  border: 2px solid #0000;
  position: absolute;
}

.region:hover {
  transform: scale(1.1);
}

#loginForm button {
  background-color: var(--accentpink);
  width: 85%;
  color: var(--moonglow);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 10px;
  font-family: Cairo, sans-serif;
  font-size: 1em;
  font-weight: bolder;
}

#loginForm button:hover {
  background: var(--lighterviolet);
  color: var(--background-dark);
}

#gameContainer header {
  background: var(--ligtherblue);
  z-index: 5;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  font-family: Cairo, sans-serif;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 8px #00000080;
}

#gameContainer header h1 {
  color: var(--moonglow);
  margin: 0;
  font-family: Cairo, sans-serif;
  font-size: 1.8em;
}

#score {
  font-family: Cairo, sans-serif;
  font-size: 1.5em;
  font-weight: bold;
}

#score.positive {
  color: var(--green);
}

#score.negative {
  color: var(--red);
}

main {
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Cairo, sans-serif;
  display: flex;
}

#constellationMap {
  aspect-ratio: 16 / 9;
  cursor: pointer;
  background: url("stellarium-001.650849d3.png") center / contain no-repeat;
  border-radius: 10px;
  width: 100%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 20px #00000080;
}

#actionBar {
  background: var(--ligtherblue);
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 40px;
  height: 120px;
  transition: width .3s, height .3s;
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  box-shadow: -5px 0 15px #000c;
}

#actionBar:hover {
  align-items: flex-start;
  width: 150px;
  height: auto;
  padding: 10px;
}

#actionBar button {
  background: var(--accentpink);
  width: 100%;
  color: var(--background-dark);
  cursor: pointer;
  text-align: left;
  white-space: nowrap;
  border: none;
  border-radius: 5px;
  margin: 5px 0;
  padding: 10px;
  font-family: Cairo, sans-serif;
  font-size: 1em;
  font-weight: bolder;
  display: none;
}

#actionBar:hover button {
  display: block;
}

